@import '../../style/App.scss';
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

.titleContact
{
  display: flex;
}

.team_info {
  width: 700px;
  height: 470px;
  margin: 0 auto;
  margin-top: 60px;
  position: relative;
  border: 4px solid $light-color;

}

.subtext
{
  font-family: Inter;
  font-weight: 600;
  font-size: 14px;
}

.message
{
  font-family: Inter;
  font-weight: 700;
  font-size: 30px;
  padding-bottom: 30px;
}



.small__button {
  button {
    margin-top: 15px;
    font-family: Inter;
    font-size: 12px;
    font-weight: 700;
    letter-spacing: 1.63px;
    text-transform: uppercase;
    padding: 23px 30px;
    box-shadow: rgba(3, 3, 3, 0.12) 0px 32px 54px;
    border-radius: 50rem;
    display: inline-flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    justify-content: space-between;
    user-select: none;
    transform: perspective(1px) translateZ(0px);
    position: relative;
    overflow: hidden;
    border: none;
    white-space: nowrap;
    color: rgb(255, 255, 255);
    background-color: rgb(73, 95, 239);
    transition: all 0.4s ease-out 0s;
    outline: none !important;
  }
  button:hover {
    transform: translateY(-10px);
    box-shadow: rgba(65, 50, 50, 0.14) 0px 32px 54px;
  }
}

input {
  background-color: $light-color;
  width: 100%;
  max-width: 570px;
  height: 45px;
  border: 0px;
  padding: 0 15px;
  font-size: 12px;
  border: 2px solid $light-color;
  outline: none;
  margin: 5px 0;
}
input:active, input:focus {
  border: 2px solid #495fef;
}

::placeholder {
  color: $black-color;

}


textarea {
  background-color: $light-color;
  width: 100%;
  max-width: 570px;
  border: 0px;
  padding: 15px;
  font-size: 12px;
  border: 2px solid $light-color;
  outline: none;
  margin: 5px 0;
}

textarea:active, textarea:focus {
  border: 2px solid #495fef;
}


::placeholder {
  color: $black-color;
  font-family: Inter;
  font-weight: 500;
  letter-spacing: 1px;
  font-size: 12px;
}

  .sending-btn 
  {
    width: 170px;
    background-color: rgb(73, 95, 239);
    border: 1px solid $white-color;
    cursor: pointer;
    min-height: 52px;
    padding: 7px 0;
    margin: 0px;
    border-radius: 5rem;

    div {
      margin: 0 auto;
      width: 30px;
      height: 30px;


      .sbl-circ {
        color: $black-color;
        position: relative;
        display: inline-block;
        border: 5px solid;
        border-radius: 50%;
        border-top-color: transparent;
        animation: rotate 1s linear infinite; 
      }
      @keyframes rotate {
        0% {
          transform: rotate(0); }
        100% {
          transform: rotate(360deg); 
        } 
      }

    }
  }





  @media screen and (max-width: 990px) {
  #contact {
    text-align: center;
    margin-top: 10px;
  }
  .sending-btn {
    margin: 30px auto;
  }

}