@import '../../../style/App.scss';

.instagram
{
  background-size: 20px 30px;
}

.contact__social {
  width: 100%;
  margin: 40px 0 60px 0;
  transition: 0.1s ease;
  cursor: pointer;
}

.contact__social:hover {
  filter: invert(70%) sepia(100%) saturate(6832%) hue-rotate(226deg) brightness(99%) contrast(90%);
}



@media screen and (max-width: 990px) {


  .contact__social {
    text-align: center;
    width: 100%;
    img {
      margin: 0 auto;
    }
  }
}