@import '../../style/App.scss';
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@600;700;800;900&display=swap');

.video-responsive {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  height: 0;
}

.video-responsive iframe {
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute;
}

.motion
{
  font-family: Inter;
  font-weight: 700;
  font-size: 60px;
  margin-bottom: 50px;
  padding-top: 50px;
}

.video-motion
{
  margin-bottom: 35px;
}

#portfolio {
  padding: 30px 0 40px 0;

  .portfolio__nav 
  {
    ul 
    {
      margin-left: -2%;
      display: flex;
      li 
      {
        font-family: Inter;
        font-size: 13px;
        line-height: 21px;
        letter-spacing: 1.45px;
        padding: 5px 15px;
        margin-right: 10px;
        cursor: pointer;
        transition: 0.3s ease;
      }
    }
    .portfolio__nav-active 
    {
      color: #495fef;
    }
  }
  .portfolio__filter 
  {
    display: flex;
    justify-content: flex-end;
    cursor: pointer;
    margin-bottom: 50px;
    background-attachment: fixed;
    max-width: 200px;
    float: right;
    position: relative;
    padding: 8px 10px;

    p 
    {
      font-family: Inter;
      font-weight: 600;
      font-size: 13px;
      padding-right: 10px;
      letter-spacing: 1.63px;
    }

    .portfolio__filter-menu {
      background-color: $light-color;
      position: absolute;
      top: 40px;
      left: 0;
      width: 100%;
      height: 100px;
      text-align: center;
      border-radius: 5px;
      z-index: 999;
      p {
        font-family: Inter;
        font-weight: 700;
        font-size: 13px;
        padding: 8px 0;
        margin: 2px 0;
        transition: 0.3s ease;
      }
      p:hover {
        background-color: rgba(73, 95, 239, 0.44);
      }
    }
  }
}

.my-masonry-grid {
  display: -webkit-box; /* Not needed if autoprefixing */
  display: -ms-flexbox; /* Not needed if autoprefixing */
  display: flex;
  margin-left: -30px; /* gutter size offset */
  width: auto;
}
.mint__gallery {
  padding-left: 30px; /* gutter size */
  background-clip: padding-box;
}
 
/* Style your items */
.mint__gallery > div { /* change div to reference your elements you put in <Masonry> */
  margin-bottom: 30px;
}

@media screen and (max-width: 767px) {
  #portfolio {
    .portfolio__nav {
      ul {
        justify-content: center;
      }
    }
    .portfolio__filter {
      justify-content: center;
      max-width: 200px;
      margin: 30px auto;
      float: none;

    }
  }

  .motion
  {
    line-height: 60px;
  }

  .yutub
  {
    margin-bottom: 30px;
  }
}

@media screen and (max-width: 400px) {
  #portfolio {
    .portfolio__nav {
      ul {
        li {
          padding: 5px 5px;
          margin-right: 5px;
        }

      }
    }
  }
}
