@import '../../style/App.scss';
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@700;800;900&display=swap');

.interest
{
  padding-top: 50px;
}

.buttonInterested
{
  text-align: center;
  padding-top: 40px;
  padding-bottom: 10px;
}

.talk
{
  font-family: Inter;
  font-size: 13px;
  font-weight: 700;
  letter-spacing: 1.63px;
  text-transform: uppercase;
  padding: 23px 30px;
  box-shadow: rgba(3, 3, 3, 0.12) 0px 32px 54px;
  border-radius: 50rem;
  display: inline-flex;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: justify;
  justify-content: space-between;
  user-select: none;
  transform: perspective(1px) translateZ(0px);
  position: relative;
  overflow: hidden;
  border: none;
  white-space: nowrap;
  color: rgb(255, 255, 255);
  background-color: rgb(73, 95, 239);
  transition: all 0.4s ease-out 0s;
  outline: none !important;
}

.icon
{
  margin-right: 15px;
  font-size: 20px;
}

.talk:hover
{
  transform: translateY(-10px);
  box-shadow: rgba(65, 50, 50, 0.14) 0px 32px 54px;
}

.smallTitle
{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 40px;
}

.circle
{
  height: 12px;
  width: 12px;
  background-color: #495fef;
  border-radius: 50%;
  margin-right: 13px;
}

.open
{
  font-family: Inter;
  font-weight: 700;
  font-size: 13px;
  letter-spacing: 1.63px;
  line-height: 1.63;
}

.contactText
{
  font-family: Inter;
  font-weight: 700;
  font-size: 50px;
  letter-spacing: -2.5px;
  color: #161c2d;
  text-align: center;
  line-height: 62px;
}

.blog {
  background-color: $light-color;
  padding: 60px 0;
}

.contactText1
{
  font-family: Inter;
  font-weight: 700;
  font-size: 50px;
  letter-spacing: -2.5px;
  color: #161c2d;
  text-align: center;
  line-height: 62px;
  display: none;
}

@media screen and (max-width: 990px) {
  .contactText
  {
    display: none;
  }

  .contactText1
  {
    display: block;
  }
}