@import '../../../style/App.scss';
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@700;800;900&display=swap');

.titel
{
  font-family: Inter;
  font-weight: 700;
  font-size: 70px;
}

@media screen and (max-width: 990px) {
  .big__title {
    width: 100%;
    text-align: center;
  }
  .titel
  {
    line-height: 70px;
  }
}


@media screen and (max-width: 400px) {
  .big__title {
    h2 {
      font-size: 4rem;
    }
  }
}