@import '../../style/App.scss';
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700;800&display=swap');

body
{
  background: url(./../hero/latar.webp);
  background-position-x: center;
  background-position-y: top;
  background-repeat: no-repeat;
}

.hero 
{
  padding-top: 245px;
  width: 100%;
  padding-bottom: 60px;
  
  .skills
  {
    font-family: Inter;
    font-weight: 700;
    font-size: 13px;
    letter-spacing: 1.63px;
  }

  .heroText
  {
    padding-top: 38px;
    font-family: Inter;
    font-weight: 800;
    font-size: 60px;
    line-height: 70px;
    padding-bottom: 55px;
    letter-spacing: -2.2px;
  }
}

@media screen and (max-width: 1080px) {
  .hero {
    padding-top: 140px;

    .hero-info {
      padding-top: 0px;
      font-size: 40px;
    }

    .hero-image {
      float: none;
      margin: 0 auto;
      margin-top: 50px;
    }
  }
}

@media screen and (max-width: 990px) {
  .hero {

    .skills
    {
      padding-top: 45px;
    }
    .hero-info {
      text-align: center;
      font-size: 40px;
    }
  }
}