@import '../../style/App.scss';
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@600;700&display=swap');


.footer 
{
  background-color: $black-color;
  width: 100%;
  min-height: 180px;

  .footer-box {
    min-height: 180px;
    display: flex;
    align-items: center;
 
    .logoFooter
    {
      font-family: Inter;
      font-weight: 700;
      color: white;
      font-size: 25px;
    }

    .copyright
    {
      font-family: Inter;
      font-weight: 500;
      color: white;
      font-size: 12px;
    }

    .top
    {
      font-family: Inter;
      font-weight: 500;
      color: white;
      font-size: 12px;
      letter-spacing: 1px;
    }
  }

  .back-to-top {
    float: right;
    cursor: pointer;
    img {
      margin-left: 10px;
    }
  }

  .back-to-top:hover {
    p {
      color: rgb(73, 95, 239);
    }
  }
}


@media screen and (min-width: 720px) 
{
  .logoFooter
  {
    margin-left: 20px;
  }

  .copyright
  {
    margin-left: 50px;
  }
}

@media screen and (max-width: 990px) 
{
  .logoFooter
  {
    justify-content: center;
  }

  .copyright
  {
    justify-content: center;
    margin-top: 10px;
  }
}
@media screen and (max-width: 580px) {
  .footer 
  {
    .footer-box 
    {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      
      img 
      {
        padding-bottom: 30px;
      }
    }
    .back-to-top 
    {
      display: flex;
      justify-content: center;
      align-items: center;
      float: none;
      flex-direction: row;
      
      img {
        padding-bottom: 0px;
      }
    }
  }
}
