  @import '../../../style/App.scss';
  
  .portfolio__box {
    position: relative;
    max-width: 370px;
    margin: 15px auto;
    cursor: pointer;
    

    img {
      width: 100%;
      height: auto;
    }

    video
    {
      width: 100%;
      height: auto;
    }

    .portfolio__hover-info 
    {
      background-color:rgba(255, 255, 255, 0.9);
      box-sizing: border-box;
    min-width: 0px;
    position: absolute;
    overflow: hidden;
    bottom: 20px;
    left: 20px;
    right: 20px;
    opacity: 0;
    border-radius: 8px;
    z-index: 1;
    padding: 1.25rem 1.875rem;
    transition: all 0.4s ease 0s;
      
    .tag
    {
      font-family: Inter;
      font-weight: 700;
      font-size: 13px;
      letter-spacing: 1.63px;
      color: rgba(22, 28, 45, 0.7);
      text-transform: uppercase;
    }

    .title
    {
      font-family: Inter;
      font-weight: 700;
      font-size: 21px;
      letter-spacing: -0.5px;
    line-height: 1;
    color: rgb(22, 28, 45);
    }

    .text-hover
    {
      text-align: left;
      margin-left: -36%;
      padding-bottom: 3%;
    }

    }
    .portfolio__hover-info:hover {
      opacity: 1;
    }
  }