@import '../../style/App.scss';
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');


@media screen and (min-width: 991px) 
{
  .contactSection 
  {
    height: 470px;
    width: auto;
    margin: 0 auto;
    border: 4px solid $light-color;

    h2 
    {
      font-family: Inter;
      font-weight: 800;
      font-size: 35px;
      letter-spacing: 1.63px;
      text-align: center;
    }

    p 
    {
      text-align: center;
      margin-top: 20px;
      font-family: Inter;
      font-weight: 700;
      font-size: 13px;
      letter-spacing: 1.63px;
      line-height: 1.63;
      transition: all 0.3s ease-out 0s;
    }

    p:hover
    {
      transform: translateY(-10px);
    }
  }
}



@media screen and (max-width: 990px) 
{
  .contactSection
  {
    padding-top: 110px;
    padding-bottom: 30px;
    border: 0px;
  
   h2 
    {
      font-family: Inter;
      font-weight: 800;
      font-size: 35px;
      letter-spacing: 1.63px;
      text-align: center;
    }

    p 
    {
      text-align: center;
      margin-top: 20px;
      font-family: Inter;
      font-weight: 700;
      font-size: 13px;
      letter-spacing: 1.63px;
      line-height: 1.63;
      transition: all 0.3s ease-out 0s;
    }
    
    p:hover
    {
      transform: translateY(-10px);
    }
  }
}