@import '../../style/App.scss';
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@600;700;800;900&display=swap');

#about 
{
  margin-bottom: 50px;
}

.sub-title
{
  font-family: Inter;
  font-weight: 600;
  font-size: 14px;
}

.sub-title-mobile
{
  font-family: Inter;
  font-weight: 600;
  font-size: 14px;
  display: none;
}

@media screen and (max-width: 990px) 
{

  .sub-title
  {
    display: none;
  }

  .sub-title-mobile
  {
    display: block;
  }

  #about 
  {
    text-align: center;
    margin-top: 20px;
  }

}